<template>
  <div class="replay_content">
    <!-- 回复评论顶部区 -->
    <van-nav-bar>
      <span slot="title">{{ comment.reply_count }} 条回复</span>
      <van-icon slot="left" name="cross" @click="$emit(`close`)" />
    </van-nav-bar>
    <!-- 当前评论 -->
    <comment-item class="origin_comment" :comment="comment"></comment-item>
    <!-- 所有当前评论回复 -->
    <comment-list
      title="全部回复"
      type="c"
      :list="commentList"
      :source="comment.com_id"
    ></comment-list>
    <!-- 底部 -->
    <div class="article_btm">
      <van-button
        class="comment_btn"
        type="default"
        round
        size="small"
        @click="isPostShow = true"
        >写评论</van-button
      >
    </div>
    <!-- 发布评论回复框 -->
    <van-popup class="write_place" v-model="isPostShow" position="bottom">
      <post-comment
        :target="comment.com_id"
        :articleId="articleId"
        @postSuccess="onPostSuccess"
      ></post-comment>
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'ReplayComment',
  props: {
    comment: {
      type: Object,
      require: true,
    },
    articleId: {
      type: [String, Number, Object],
      required: true,
    },
  },
  components: {
    CommentItem: () => import('./comment_item.vue'),
    commentList: () => import('./comment.vue'),
    PostComment: () => import('./comment_post.vue'),
  },

  data() {
    return {
      isPostShow: false, // 编辑评论对话框是否显示
      commentList: [], // 评论回复数据
    };
  },
  methods: {
    // 文章评论回复成功
    onPostSuccess(comment) {
      this.comment.reply_count += 1;
      this.commentList.unshift(comment);
      this.isPostShow = false;
    },
  },
  created() {},
};
</script>

<style lang="less" scoped>
.article_btm {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  border-top: 1px solid #eee;
  background: #fff;
  font-size: 16px;
  .comment_btn {
    width: 200px;
    font-size: 13px;
    height: 24px;
    line-height: 24px;
    padding: 0 40px;
    color: #777;
  }
}
.write_place {
  box-sizing: border-box;
  padding: 6px;
  .post_comment {
    display: flex;
    align-items: flex-end;
    .comment_textarea {
      border: 1px solid #ccc;
    }
    .sub_btn {
      width: 80px;
    }
  }
}
</style>
